import { Injectable, OnInit } from '@angular/core';
// import { Variable } from '../../providers/model/variable';
import { ToolsProvider } from '../tools/tools';
import { LoadingController, Events } from 'ionic-angular';
import { DeviceRequestsProvider } from '../../providers/tools/requests';
import { variable } from '@angular/compiler/src/output/output_ast';
import { Variable } from '../model/variable';

declare let Baiduasrtts: any;

@Injectable()
export class SpeechHelperProvider implements OnInit {
    speakText: string;
    loading: any;
    timeout: any;
    isfirst: boolean = true;
    constructor(
        private tools: ToolsProvider,
        private events: Events,
        private loadingCtrl: LoadingController,
        private device: DeviceRequestsProvider
    ) {
        try {
            if (Baiduasrtts) {
                Baiduasrtts.startSpeechRecognize(null, e => {
                }, r => {
        
                });

            }
        } catch (err) {

        }

    }
    ngOnInit() {
    }

    presentLoadingCustom() {
        this.loading = this.loadingCtrl.create({
            spinner: 'dots',
            content: ''
        });
        this.loading.present();
        this.timeout = setTimeout(() => {
            if (this.loading != null) {
                this.loading.dismiss();
                this.initTTSconfig("")
                this.tools.presentToast("")
            }
        }, 10000);
    }
    dismissLoading() {
        if (this.loading != null) {
            this.loading.dismiss();
            this.loading = null;
            clearTimeout(this.timeout);
        }
    }
    startSpeech() {
        this.startSpeechRecognize();
        // if (this.isfirst) {
        //   this.startSpeechRecognize();
        //     this.isfirst = false;
        // }
    }
    private startSpeechRecognize() {
        // 
        Baiduasrtts.startSpeechRecognize(null, e => {
        }, r => {

        });
        // 


        // 
        Baiduasrtts.addEventListener((res: any) => {
            // restype
            if (!res) {
                return;
            }

            switch (res.type) {
                case "asrReady": {
                    // 
                    // document.getElementById('v_status').innerHTML = "";
                    this.presentLoadingCustom();
                    break;
                }

                case "asrBegin": {
                    // 
                    // document.getElementById('v_status').innerHTML = ""

                    //  alert("");

                    break;
                }

                case "asrEnd": {
                    // 

                    // this.initTTSconfig(this.speakText);
                    // document.getElementById('v_status').innerHTML = ""
                    //  alert("");

                    break;
                }

                case "asrText": {
                    // 
                    // alert(JSON.stringify(res));
                    this.speakText = res.message.results_recognition;
                    // alert(this.speakText);
                    break;
                }

                case "asrFinish": {
                    // 
                    this.dismissLoading();
                    let rese = `${this.speakText}`;
                    this.dealData(rese);
                    // Baiduasrtts.synthesizeSpeech(rese, (e) => { }, (r) => { });
                    // this.initTTSconfig(rese);

                    break;
                }

                case "asrCancel": {
                    // 

                    break;
                }

                default:
                    break;
            }

        }, (err) => {
            // document.getElementById('v_status').innerHTML = "";
            this.dismissLoading();
            this.tools.presentToast("")
            this.initTTSconfig("");
        });
    }
    private dealData(data: string) {
        this.device.getSpeechDataList(data).then((res: any) => {

            if (res.success) {
                let temp = res.data;
                switch (temp.type) {
                    case 'mode': {
                        Variable.socketObject.setMode(temp.id, temp.name, true);
                        break;
                    } case 'device': {
                        Variable.socketObject.setDeviceState(temp.id, temp.name, temp.state, true);
                        break;
                    }
                }
            } else {
                let text = `${res.message}`;
                this.initTTSconfig(text);
            }
        });
    }

    initTTSconfig(data: string) {
        Baiduasrtts.initTTSconfig((e) => { }, (r) => { });
        Baiduasrtts.synthesizeSpeech(data, (e) => { }, (r) => { });
    }
}


